import logoSrc from '../../assets/logo-transparente.png';

export default function Footer() {
  return (
    <section className="footer">
      <div className="footer__container">
        <img
          src={logoSrc}
          className="footer__logo"
          width={400}
          height={70}
          alt="logo"
        />
        <p className="footer__credits"> 2024 Julin Reyes Botello</p>
      </div>
      <p className="footer__hotmart-txt">
        "Este producto se comercializa con el apoyo de Hotmart. La plataforma no
        realiza un control editorial previo de los productos vendidos, ni evala
        el tecnicismo y experiencia de quienes los elaboran. La existencia de un
        producto y su adquisicin, a travs de la plataforma, no puede
        considerarse garanta de calidad de contenido y resultado, en ningn
        caso. Al comprarlo, el comprador declara conocer esta informacin. Puede
        acceder a los trminos y polticas de Hotmart, incluso antes de que se
        complete la compra."
      </p>
    </section>
  );
}
