import { motion } from 'framer-motion';

export default function CookiesNotice({
  cookiesEstablished,
  onCookiesAccept,
  onCookiesReject,
}) {
  return (
    <section className={`${cookiesEstablished ? 'hidden' : 'cookies'}`}>
      <div className="cookies__container">
        <h2 className="cookies__title">Este sitio usa cookies</h2>
        <p className="cookies__text">
          Esta pgina usa cookies para brindarte una mejor experiencia
          almacenando y procesando datos de usuario. Es posible que terceros
          incluido Meta, usen cookies, balizas web y otras tecnologas de
          almacenamiento para recopilar o recibir informacin y as proporcionar
          servicios de medicin, y segmentacin y entrega de anuncios.
        </p>
        <p className="cookies__text">
          Puede aceptar el uso de estas tecnologas dando click en Aceptar o
          negar su consentimiento haciendo click en Rechazar. El rendimiento
          podra ser menor en caso de la segunda opcin.
        </p>
        <p className="cookies__text">
          Aceptando las cookies nos permites mantener nuestros niveles de
          calidad. En caso contrario perderamos la posibilidad de acompaar en
          algunos de nuestros procesos.
        </p>
        <div className="cookies__container_btns">
          <motion.button
            className="cookies__btn cookies__btn_aceptar"
            whileHover={{
              scale: 1.1,
            }}
            whileTap={{ scale: 0.9 }}
            onClick={onCookiesAccept}
          >
            Aceptar
          </motion.button>
          <motion.button
            className="cookies__btn"
            whileHover={{
              scale: 1.1,
            }}
            whileTap={{ scale: 0.9 }}
            onClick={onCookiesReject}
          >
            Rechazar
          </motion.button>
        </div>
      </div>
    </section>
  );
}
