import { motion } from 'framer-motion';
import fondoImg from '../../assets/bio.webp';

export default function Bio() {
  return (
    <section id="bio" className="bio">
      <div className="bio__text-container">
        <motion.h3
          className="bio__subtitle"
          initial={{ y: '-0.5rem' }}
          whileInView={{ y: 0, transition: { duration: 1 } }}
        >
          Quin soy?
        </motion.h3>
        <motion.p
          className="bio__paragraph"
          initial={{ x: '-0.5rem' }}
          whileInView={{ x: 0, transition: { duration: 1 } }}
        >
          Mi nombre es Julin Reyes Botello. Soy actor proferional egresado de
          la licenciatura en teatro de la UNAM. Tambin tengo un par de
          diplomados, uno de ellos internacional (Argentina-Mxico). Desde 2012,
          he dirigido, producido o actuado en ms de 15 obras.
        </motion.p>

        <motion.h3
          className="bio__subtitle"
          initial={{ x: '0.5rem' }}
          whileInView={{ x: 0, transition: { duration: 1 } }}
        >
          Lo importante es lo que viene:
        </motion.h3>
        <motion.p
          className="bio__paragraph"
          initial={{ x: '-0.5rem' }}
          whileInView={{ x: 0, transition: { duration: 1 } }}
        >
          Yo era un adolescente{' '}
          <span className="bio__text-accent">muy tmido</span>. Era bulleado,
          tena un apodo horrible, me senta feo y gordo,{' '}
          <span className="bio__text-accent">
            me escond debajo de un suter durante 7 aos{' '}
          </span>
          porque me daba vergenza que vieran mi cuerpo. Muchas cosas despus me
          enamor del teatro: el espacio donde poda vivir una realidad muy
          diferente a la ma que odiaba. Tom esa decisin hace 15 aos.{' '}
          <span className="bio__text-accent">
            Hoy mi vida es totalmente diferente.
          </span>{' '}
        </motion.p>
        <motion.p
          className="bio__paragraph"
          initial={{ x: '-0.5rem' }}
          whileInView={{ x: 0, transition: { duration: 1 } }}
        >
          <span className="bio__text-accent">
            Soy una persona tmida, pero a la vez soy extrovertido{' '}
          </span>
          (eso es posible, s). Mi profesin me ha permitido exponerme frente a
          cientos de personas:
          <span className="bio__text-accent">
            {' '}
            la timidez ya no rige mi vida.
          </span>
        </motion.p>
        <motion.p
          className="bio__paragraph"
          initial={{ x: '-0.5rem' }}
          whileInView={{ x: 0, transition: { duration: 1 } }}
        >
          Decid crear este curso para ayudar a otras personas timidaz que
          quieren
          <span className="bio__text-accent">
            {' '}
            transformar su vida y dejen atrs el miedo a relacionarse.{' '}
          </span>
        </motion.p>
      </div>
      <img
        src={fondoImg}
        alt="Julin Reyes Botello"
        height={1080}
        width={720}
        className="bio__img"
      />
    </section>
  );
}
